a {
  color: white;
  padding: 8px;
  font-family: "Poppins", sans-serif;
  font-size: 35px;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  color: #777;
}

a:active {
  text-decoration: underline;
}

li {
  text-align: center;
  margin-left: 100px;
  text-size-adjust: 10;
}
.center-text {
  text-align: center;
}

a.center-text {
  display: block;
}

#a {
  font-size: 80px;
  position: sticky;
}

.gallery-container {
  top: 200px !important;
  position: relative;
  display: flex;
  justify-content: center;
}

.thumbnails {
  position: absolute;
  bottom: 8px;
  display: flex;
  flex-direction: row;
  gap: 6px;
}

.thumbnails div {
  width: 8px;
  height: 8px;
  cursor: pointer;
  background: #aaa;
  border-radius: 100%;
}

.thumbnails div.highlighted {
  background-color: #777;
}

.slides {
  margin: 0 16px;
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;
  width: 100%;  /* Modify width to accommodate scrolling */
  padding: 0 0.25rem;
  height: 720px;
  overflow-x: auto;  /* Enable horizontal scrolling */
  overscroll-behavior-x: contain;
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
  z-index: 5;
}

.slides > div {
  scroll-snap-align: start;
}

.slides img {
  width: 540px;
  object-fit: contain;
}

.slides::-webkit-scrollbar {
  display: none;
}

